import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { IReminder } from '../models/reminder.model';
import { VSDKConfig, VSDKConfigToken } from '../angular-api.module';


@Injectable()
export class EnvelopRemindersService {

  private _config_token: VSDKConfig;
  private _rForm_backend_url: string;

  constructor(
    private http: HttpClient,
    private injector: Injector
  ) {
    this._config_token = this.injector.get(VSDKConfigToken);
    this._rForm_backend_url = this._config_token.rForm_backend_url;
  }

  getReminder(envelopeId, reminderId): Promise<IReminder> {
    return this.http.get<IReminder>(`${this._rForm_backend_url}/envelopes/${envelopeId}/reminder/${reminderId}`).toPromise()
      .then((reminder) => {
        return reminder;
      });
  }

  updateReminder(envelopeId: string, reminderId, body): Promise<IReminder> {
    return this.http
      .put<IReminder>(`${this._rForm_backend_url}/envelopes/${envelopeId}/reminder/${reminderId}`, body)
      .toPromise();
  }

  deleteReminder(envelopeId, reminderId) {
    return this.http.delete(`${this._rForm_backend_url}/envelopes/${envelopeId}/reminder/${reminderId}`).toPromise();
  }

  createReminder(envelopeId, body: { setup_time: number, interval_time: number, is_on: boolean }): Promise<IReminder> {
    return this.http.post<IReminder>(`${this._rForm_backend_url}/envelopes/${envelopeId}/reminder`, body).toPromise()
      .then((reminder) => {
        return reminder;
      });
  }

}
