import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { VSDKConfig, VSDKConfigToken } from '../angular-api.module';
import { IReminder } from '../models/reminder.model';


@Injectable()
export class TemplatesRemindersService {

  private _config_token: VSDKConfig;
  private _rForm_backend_url: string;

  constructor(
    private http: HttpClient,
    private injector: Injector
  ) {
    this._config_token = this.injector.get(VSDKConfigToken);
    this._rForm_backend_url = this._config_token.rForm_backend_url;
  }


  getReminder(templateId, reminderId): Promise<IReminder> {
    return this.http.get<IReminder>(`${this._rForm_backend_url}/templates/${templateId}/reminder/${reminderId}`).toPromise()
      .then((reminder) => {
        return reminder;
      });
  }

  updateReminder(templateId: string, reminderId, body): Promise<IReminder> {
    return this.http
      .put<IReminder>(`${this._rForm_backend_url}/templates/${templateId}/reminder/${reminderId}`, body)
      .toPromise();
  }

  deleteReminder(templateId, reminderId) {
    return this.http.delete(`${this._rForm_backend_url}/templates/${templateId}/reminder/${reminderId}`).toPromise();
  }

  createReminder(templateId, body: { setup_time: number, interval_time: number }): Promise<IReminder> {
    return this.http.post<IReminder>(`${this._rForm_backend_url}/templates/${templateId}/reminder`, body).toPromise()
      .then((reminder) => {
        return reminder;
      });
  }

}
