import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { regParse } from '../functions/regex';
import { VSDKConfig, VSDKConfigToken } from '../angular-api.module';


@Injectable()
export class ValidatorService {
  private _config_token: VSDKConfig;
  private _rForm_backend_url: string;
  private validators: any[] = [];
  private validatorsObject: any = {};

  constructor(
    private http: HttpClient,
    private injector: Injector
  ) {
    this._config_token = this.injector.get(VSDKConfigToken);
    this._rForm_backend_url = this._config_token.rForm_backend_url;
  }

  public getValidatorsArray() {
    if (this.validators && this.validators.length > 0) {
      return this.validators;
    } else {
        return this.http.get(this._rForm_backend_url + '/validators').toPromise().then((response: any[]) => {
        this.validators = response;
        return this.validators as any[];
      }, (err) => {
        return null;
      });
    }
  }

  public async getValidatorsObject() {
    this.validatorsObject = {};
    await this.getValidatorsArray();
    for (let i = 0; i < this.validators['length']; i++) {
      const name = this.validators[i]['name'];
      this.validatorsObject[name] = regParse(this.validators[i]['regex']);
    }
    return this.validatorsObject;
  }
}
