import { Injectable, Injector } from '@angular/core';
import { HttpClient, HttpRequest } from '@angular/common/http';
import { EventTrackerService } from '@verdocs/event-tracker';
import { Observable, ReplaySubject, throwError, from, forkJoin, of } from 'rxjs';
import { catchError, map, delay, mergeMap } from 'rxjs/operators';
import { findIndex } from 'lodash';

import { VSDKConfig, VSDKConfigToken } from '../angular-api.module';

import { ITemplate } from '../models/template.model';
import { IPage } from '../models/page.model';
import { IRole } from '../models/role.model';
import { IField } from '../models/field.model';
import { FieldRole } from '../models/field-role.model';

@Injectable()
export class BuilderService {
  public saveStatusSubject: ReplaySubject<string> = new ReplaySubject<string>();

  private _config_token: VSDKConfig;
  private _rForm_backend_url: string;

  constructor(
    private http: HttpClient,
    private injector: Injector,
    private eventTracker: EventTrackerService
  ) {
    this._config_token = this.injector.get(VSDKConfigToken);
    this._rForm_backend_url = this._config_token.rForm_backend_url;
  }

  createTemplate(template_body: ITemplate): Observable<ITemplate> {
    return this.http
      .post<ITemplate>(this._rForm_backend_url + '/templates', template_body)
      .pipe(
        map((res) => {
          return res;
        })
      );
  }

  uploadTemplateDocument(file: File, template: ITemplate) {
    const formdata = new FormData();
    formdata.append('document', file, file.name);
    const req = new HttpRequest('POST', `${this._rForm_backend_url}/templates/${template.id}/documents`, formdata, {
      reportProgress: true
    });
    return this.http.request(req)
      .pipe(
        map(res => res),
        catchError(err => {
          return throwError(err);
        })
      )
  }

  addTemplatePage(document, pageNum, templateId) {
    const body = {
      sequence: pageNum,
      page_number: pageNum,
      document_id: document.id
    }
    return this.http.post<IPage>(`${this._rForm_backend_url}/templates/${templateId}/pages`, body)
      .pipe(
        map((res: IPage) => res),
        catchError(err => {
          return throwError(err);
        })
      )
  }

  addTemplatePages(document, pageNums: number[], templateId: string) {
    return from(pageNums)
      .pipe(
        mergeMap(pageNum => {
          const page = {
            sequence: pageNum,
            page_number: pageNum,
            document_id: document.id
          };
          return <Observable<IPage>>this.http.post(`${this._rForm_backend_url}/templates/${templateId}/pages`, page).pipe(delay(200));
        })
      )
  }

  addTemplateField(newField: IField, i, j, template: ITemplate): Observable<ITemplate> {
    newField.name = newField.name.trim();
    return this.http
      .post<IField>(this._rForm_backend_url + '/templates/' + newField.template_id + '/fields', newField)
      .pipe(
        map(field => {
          this.eventTracker.createEvent({
            category: 'document',
            action: `document ${field.type} field added`,
            label: `document id: ${newField.template_id}`
          });
          template.pages[i].fields[j] = field;
          // this.checkForFields(template.roles);
          const roleIndex = findIndex(template.roles, { name: field.role_name });
          if (roleIndex >= 0) {
            template.roles[roleIndex]['fields'].push(field);
          }
          // this.templateSubject.next(template);
          return template;
        })
      );
  }

  deleteTemplateField(template: ITemplate, fieldName: string, i, j): Observable<ITemplate> {
    return this.http
      .delete<void>(`${this._rForm_backend_url}/templates/${template.id}/fields/${fieldName.trim()}`)
      .pipe(
        map(field => {
          const fieldIndex = findIndex(template.pages[i].fields, { name: fieldName });
          const roleIndex = findIndex(template.roles, { name: template.pages[i].fields[fieldIndex].role_name });

          if (roleIndex > -1) {
            const roleFieldIndex = findIndex(template.roles[roleIndex].fields, { name: fieldName });
            if (roleFieldIndex > -1) {
              template.roles[roleIndex].fields.splice(roleFieldIndex, 1);
            }
          }
          template.pages[i].fields.splice(fieldIndex, 1);
          // this.updateLocalTemplate(template);
          // this.saveStatusSubject.next('saved');
          // this.checkForFields(template.roles);
          return template;
        })
      );
  }

  updateRoleField(template: ITemplate, updated_field, role_index, old_name) {
    const field_index = findIndex(template.roles[role_index].fields, { name: old_name });
    if (field_index >= 0) {
      template.roles[role_index].fields[field_index] = updated_field;
    } else {
      template.roles[role_index].fields.push(updated_field);
    }
    return template;
  }

  updateTemplateField(template: ITemplate, body: IField, oldName, i, j): Observable<ITemplate> {
    oldName = oldName.trim();
    return this.http
      .put<IField>(this._rForm_backend_url + '/templates/' + template.id + '/fields/' + oldName, body)
      .pipe(
        map(field => {
          this.eventTracker.createEvent({
            category: 'document',
            action: `document ${field.type} field updated`,
            label: `document id: ${template.id}`
          });
          const fieldIndex = findIndex(template.pages[i].fields, { name: oldName });
          if (field.page_sequence - 1 !== i) {
            template.pages[i].fields.splice(fieldIndex, 1);
            template.pages[field.page_sequence - 1].fields.push(field);
          } else {
            template.pages[i].fields[fieldIndex] = field;
          }
          const roleIndex = findIndex(template.roles, { name: field.role_name });
          if (roleIndex >= 0) {
            template = this.updateRoleField(template, field, roleIndex, oldName);
          }
          // this.templateSubject.next(template);
          this.saveStatusSubject.next('saved');
          return template;
        })
      );
  }

  updateDropdownField(template: ITemplate, body: any, oldName, i, j): Observable<ITemplate> {
    oldName = oldName.trim();
    return this.http
      .put<any>(this._rForm_backend_url + '/templates/' + template.id + '/fields/' + oldName, body)
      .pipe(
        map(field => {
          const fieldIndex = findIndex(template.pages[i].fields, { name: oldName });
          template.pages[i].fields[fieldIndex] = field;
          // this.templateSubject.next(template);
          this.saveStatusSubject.next('saved');
          return template;
        })
      );
  }

  addFieldRole(templateId: string, fieldId: string, roleId: string, i, j): Promise<FieldRole> {
    const fieldRoleInfo = JSON.stringify({
      field_id: fieldId,
      role_id: roleId
    });
    return this.http
      .post<FieldRole>(this._rForm_backend_url + '/template/' + templateId + '/field_role', fieldRoleInfo).toPromise().then(fieldRole => {
        return fieldRole;
      });
  }

  addRole(role: IRole | IRole[], template: ITemplate): Observable<ITemplate> {
    const templateBackend = this._rForm_backend_url + '/templates/' + template.id + '/roles';
    let roles = [];
    roles = roles.concat(role);
    const roleRequests = [];
    roles.forEach(role => {
      roleRequests.push(this.http.post(templateBackend, role));
    })

    if (roles && roles.length > 0) {
      return forkJoin(roleRequests).pipe(
        map(savedRoles => {
          savedRoles.concat(savedRoles as IRole[]);
          if (!template['roles']) {
            template['roles'] = <IRole[]>[];
          }
          const savedRolesWithFields = savedRoles.map(newRole => {
            newRole['fields'] = [];
            return newRole;
          });
          template['roles'] = template['roles'].concat(savedRolesWithFields);
          // this.recipients = this.sortRoles(template);
          // this.roles = this.recipients;
          // this.templateSubject.next(template);
          // this.recipientsSubject.next(this.recipients);
          // this.recipients;
          return template
        }, (err) => {
          console.error(err);
          console.error('Couldn\'t save all the roles');
          return err;
        })
      );
    } else {
      return of(template);
    }
  }

  deleteRole(roleName, template: ITemplate) {
    return this.http.delete(this._rForm_backend_url + '/templates/' + template.id + '/roles/' + roleName)
      .toPromise()
      .then(() => {
        // remove(template.roles, (role: IRole) => {
          // this.recipients = this.sortRoles(template);
          // if (role.name === roleName) {
          //   const fields = role.fields;
          //   const fieldNames = [];
          //   for (const field of fields) {
          //     fieldNames.push(field.name);
          //   }
          //   this.deleteTemplateFields(fieldNames, template);
          // }
          // this.templateSubject.next(template);
          // this.recipientsSubject.next(this.recipients);
        //   return role.name === roleName
        // });
      });
  }

  deleteRoles(roleNames: Array<string>, template: ITemplate) {
    const deleteCalls = [];
    roleNames.forEach(role_name => {
      template = {
        ...template,
        roles: template.roles.filter(role => role.name !== role_name)
      }
      deleteCalls.push(this.http.delete(this._rForm_backend_url + '/templates/' + template.id + '/roles/' + role_name))
    });
    return forkJoin(deleteCalls).pipe(
      map(() => {
        return template;
      }, err => {
        console.error(err);
        console.error('Couldn\'t delete all the roles');
        return err;
      })
    )
  }

  updateRoles(roles, template: ITemplate): Observable<ITemplate> {
    const updateCalls = [];
    roles.forEach(role => {
      const body = {
        template_id: template.id,
        name: role.name.trim(),
        full_name: role.full_name,
        email: role.email,
        sequence: role.sequence,
        type: role.type,
        delegator: role.delegator,
        message: role.message,
        phone: role.phone
      }

      const role_index = findIndex(template.roles, { name: role.old_name });
      if (role_index >= 0) {
        template.roles[role_index] = {
          ...template.roles[role_index],
          template_id: template.id,
          name: role.name.trim(),
          full_name: role.full_name,
          email: role.email,
          sequence: role.sequence,
          type: role.type,
          delegator: role.delegator,
          message: role.message,
          phone: role.phone
        }
      }
      this.eventTracker.createEvent({
        category: 'document',
        action: 'document role updated',
        label: `document id: ${template.id}`
      });
      updateCalls.push(this.http.put(this._rForm_backend_url + '/templates/' + template.id + '/roles/' + role.old_name, body));
    });
    return forkJoin(updateCalls)
      .pipe(
        map(() => {
          return template;
        }, (err) => {
          console.error(err);
          console.error('Couldn\'t save all the roles');
          return err;
        })
      );
  }
}